import CardPageComponent from "../components/card-page";
import {render} from "../components/utils";
import {Place} from "../components/consts";
export const Url = {
  offers: `./data/decor.json`,
  category: `./data/category.json`
};

const STATUS_OK = 200;

let offers = [];
const main = document.querySelector(`main`);

const getImage = (img) => {
  if (typeof (img) === `string`) {
    return [img];
  } else {
    return img.filter((it) => it.endsWith(`jpg`));
  }
};

const getOffers = (data) => {
  return data.reduce((cardList, card) => {
    const product = {
      title: card.name,
      price: card.price,
      sale: card.wholesalePrice,
      images: getImage(card.picture),
      categoryId: card.categoryId,
      id: card.id,
      material: card.material,
      article: card.vendorCode
    };
    cardList.push(product);
    return cardList;
  }, []);
};

export const load = (onload, url) => {
  const xhr = new XMLHttpRequest();
  xhr.responseType = `json`;

  xhr.addEventListener(`load`, () => {
    if (xhr.status === STATUS_OK) {
      onload(xhr.response);
    }
  });

  xhr.open(`GET`, url);
  xhr.send();
};

const getCard = (offs, id) => {
  offs.forEach((card) => {
    if (card.id === id) {
      const cardPageComponent = new CardPageComponent(card);
      render(main, cardPageComponent.getElement(card), Place.BEFOREEND);
    }
  });
};

export const loadCard = (data) => {
  offers = getOffers(data);
  let strGET = window.location.search.replace(`?`, ``);
  getCard(offers, strGET);
};
